export interface WaitingListInterface {
  user: string;
  period: string;
  position: string;
  waiting_status: WaitingListStatus;
  status: number;
  user_position_marks: number;
  createdAt: string;
  updatedAt: string;
  id: string;
}

export enum WaitingListStatus {
  // when user have Passed Exam and Waiting To Be Placed to exam
  Waiting = "Waiting",
  // when user have been Placed to school
  Placed = "Placed",
  // when user have been Failed exam
  Failed = "Failed",
}

export enum applicantGender {
  male = "Male",
  female = "Female",
  null = "-",
}

