import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import {
  WaitingListInterface,
  WaitingListStatus,
} from "../interfaces/waitingList";

/**
 * * ****************************** INTERFACES *****************************
 */

// GET WAITING LIST
export interface GetWaitingListAction {
  type: ActionTypes.GET_WAITING_LIST;
  payload: WaitingListInterface[];
}

export type WaitingListActionType = GetWaitingListAction;

/**
 * * ****************************** ACTIONS *****************************
 */

// GET WAITING LIST
export const getWaitingList = (
  position: string,
  callback: Function,
  waiting_status: WaitingListStatus = WaitingListStatus.Waiting,
  sortBy: "createdAt" | "position" = "createdAt"
) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.post<WaitingListInterface[]>(
        `${API.DO_EXAM}/waiting`,
        {
          order: "desc",
          sortBy,
          filter: {
            waiting_status,
            position,
          },
        }
      );
      console.log({ api_waiting: res.data });

      dispatch<GetWaitingListAction>({
        type: ActionTypes.GET_WAITING_LIST,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};
